import { useSelector } from 'react-redux';
import { Link, useParams } from 'react-router-dom';
import { IChatState, ICHat } from '../common/models';

export const ChatList = () => {
    const { profileId } = useParams();
    const { chatList, isLoading } = useSelector((state: IChatState) => state);
    const chatListMarkup = chatList.map(
        (chat: ICHat) =>
            chat.dialog && (
                <div
                    className="border p-2 mb-2 d-flex align-items-center flex-column flex-sm-row position-relative"
                    key={chat.id}
                >
                    <div className="flex-shrink-0 me-sm-2 ">
                        <b className="me-2">{chat.human.name ?? 'Unknown'}</b>
                        <span style={{ color: 'red' }}>
                            {chat.human.age ?? 'unknown'}
                        </span>
                    </div>
                    <div className="flex-fill text-sm-end text-break">
                        {chat.dialog.message}
                    </div>
                    <Link
                        className="stretched-link"
                        to={
                            profileId
                                ? `./chat/${chat.human.id}`
                                : `/${chat.human.id}`
                        }
                    />
                </div>
            )
    );
    return (
        <div className="row justify-content-center">
            <div className="col-sm-10 col-md-8 col-lg-6 mb-2">
                {!isLoading && chatListMarkup}
                {!isLoading && !chatList.length && (
                    <h2 className="text-center">No chats found..</h2>
                )}
            </div>
        </div>
    );
};
