import { useSelector } from 'react-redux';
import { IMG_DOMEN } from '../app/config';
import { IChatState } from '../common/models';

export const ChatGallery = () => {
    const { imageList } = useSelector((state: IChatState) => state);
    return (
        <div
            className="row gy-2 mt-2"
            style={{ maxHeight: '80vh', overflow: 'auto' }}
        >
            {imageList
                .slice()
                .reverse()
                .map(image => (
                    <div className="col-6 col-sm-2 col-md-3" key={image.id}>
                        <img
                            src={IMG_DOMEN + image.src}
                            alt={image.alt}
                            className="w-100"
                        />
                    </div>
                ))}
            {imageList.length ? '' : <h2 className="text-center">No images</h2>}
        </div>
    );
};
