import { REFRESH_TOKEN } from './../../app/config';
import { IMessage, IImage } from './../../common/models/index';
import { ICHat, IChatState } from '../../common/models/index';
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';
import axios from 'axios';

const initialState: IChatState = {
    token: localStorage.getItem('token') ?? '',
    chatList: [],
    chatMessages: [],
    imageList: [],
    isLoading: false,
};

interface GetChatMessagesArgs {
    profileId: string | undefined;
    interlocutorId: string | undefined;
}

interface GetChatListArgs {
    profileId: string | undefined;
}

export const chatSlice = createSlice({
    name: 'chat',
    initialState,
    reducers: {
        setToken: (state, action: PayloadAction<string>) => {
            localStorage.setItem('token', action.payload);
            state.token = action.payload;
        },
        setChatList: (state, action: PayloadAction<ICHat[]>) => {
            state.chatList = action.payload;
        },
        setIsLoading: (state, action: PayloadAction<boolean>) => {
            state.isLoading = action.payload;
        },
        addChatMessages: (state, action: PayloadAction<IMessage[]>) => {
            state.chatMessages.push(...action.payload);
        },
        addNewChatMessages: (state, action: PayloadAction<IMessage[]>) => {
            state.chatMessages.unshift(...action.payload);
        },
        clearChatMessages: state => {
            state.chatMessages = [];
            state.imageList = [];
        },
        addImage: (state, action: PayloadAction<IImage>) => {
            state.imageList.push(action.payload);
        },
    },
});

// Action creators are generated for each case reducer function
export const {
    setToken,
    setChatList,
    setIsLoading,
    addChatMessages,
    addNewChatMessages,
    clearChatMessages,
    addImage,
} = chatSlice.actions;

export const chatReducer = chatSlice.reducer;

const requestChatMessages = async (
    profileId: string | undefined,
    interlocutorId: string | undefined,
    offset: number,
    token: string,
    dispatch: Function,
    isUpdating = false
) => {
    const res = await axios.get(
        `https://dialog.api.dc2.a4sex.net/api/v1/users/${profileId}/dialog`,
        {
            params: {
                tid: interlocutorId,
                offset,
            },
            headers: {
                Authorization: `Bearer ${token}`,
            },
        }
    );
    const processMessages = async (messageList: IMessage[]) => {
        const data: IMessage[] = [];
        for (const message of messageList) {
            const [prefix, alias] = message.message
                .replace(/^\[\[|\]\]$/g, '')
                .split(':');
            if (prefix !== 'IMG') {
                message.isImg = false;
                data.push({ ...message, isImg: false });
                continue;
            }
            const res = await axios.get(
                `https://photo.api.dc2.a4sex.net/sends/${alias}`,
                {
                    params: {
                        tid: message.fromId,
                    },
                    headers: {
                        Authorization: `Bearer ${token}`,
                    },
                }
            );
            const photo = res.data.photo;
            const src: string = photo._links.origin.href;
            data.push({ ...message, isImg: true, src });
            if (!isUpdating)
                dispatch(
                    addImage({
                        id: photo.id as string,
                        src,
                        alt: photo.copy,
                    })
                );
        }
        return data;
    };

    return await processMessages(res.data);
};

const getNewToken = async (dispatch: Function) => {
    try {
        const res = await axios.post(
            'https://auth.api.dc2.a4sex.net/api/v1/refresh',
            {
                token: REFRESH_TOKEN,
            }
        );
        dispatch(setToken(res.data.token));
    } catch (error: any) {
        console.log(error);
    }
};

export const updateChatMessages = createAsyncThunk(
    'chat/updateChatMessages',
    async ({ profileId, interlocutorId }: GetChatMessagesArgs, thunkAPI) => {
        const { token, chatMessages, isLoading } =
            thunkAPI.getState() as IChatState;
        if (isLoading) return;
        try {
            const res = await requestChatMessages(
                profileId,
                interlocutorId,
                0,
                token,
                thunkAPI.dispatch,
                true
            );
            if (!chatMessages.length) return;
            const lastMessageId = chatMessages[0].id;
            let i = 0;
            for (const message of res) {
                if (message.id === lastMessageId) break;
                i++;
            }
            if (i) {
                thunkAPI.dispatch(addNewChatMessages(res.slice(0, i)));
            }
        } catch (error: any) {
            if (error.response) {
                if (error.response.status === 401) {
                    await getNewToken(thunkAPI.dispatch);
                    thunkAPI.dispatch(
                        updateChatMessages({ profileId, interlocutorId })
                    );
                } else {
                    console.log(error.response);
                }
            }
        }
    }
);

export const getAllChatMessages = createAsyncThunk(
    'chat/getAllChatMessages',
    async ({ profileId, interlocutorId }: GetChatMessagesArgs, thunkAPI) => {
        let offset = 0;
        while (true) {
            thunkAPI.dispatch(setIsLoading(true));
            const { token } = thunkAPI.getState() as IChatState;
            try {
                const res = await requestChatMessages(
                    profileId,
                    interlocutorId,
                    offset,
                    token,
                    thunkAPI.dispatch
                );
                thunkAPI.dispatch(addChatMessages(res));

                if (res.length < 15) {
                    thunkAPI.dispatch(setIsLoading(false));
                    break;
                }
                offset += 15;
            } catch (error: any) {
                if (error.response) {
                    if (error.response.status === 401) {
                        await getNewToken(thunkAPI.dispatch);
                    } else {
                        console.log(error.response);
                        offset += 15;
                    }
                }
            }
        }
    }
);

export const getChatList = createAsyncThunk(
    'chat/getChatList',
    async ({ profileId }: GetChatListArgs, thunkAPI) => {
        const { token } = thunkAPI.getState() as IChatState;
        thunkAPI.dispatch(setIsLoading(true));
        axios
            .get('https://dialog.api.dc2.a4sex.net/api/contact_intimates', {
                params: {
                    userId: profileId,
                },
                headers: {
                    Authorization: 'Bearer ' + token,
                },
            })
            .then(res => {
                thunkAPI.dispatch(setChatList(res.data));
                thunkAPI.dispatch(setIsLoading(false));
            })
            .catch(async error => {
                if (error.response) {
                    if (error.response.status === 401) {
                        await getNewToken(thunkAPI.dispatch);
                        thunkAPI.dispatch(getChatList({ profileId }));
                    } else console.log(error.response);
                }
            });
    }
);
