import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import { IChatState } from '../common/models';
import { ChatList } from '../components/ChatList';
import { SearchProfile } from '../components/SearchProfile';
import { getChatList } from '../features/chat/chatSlice';

export const RootPage = () => {
    const { profileId } = useParams();
    const dispatch = useDispatch();
    const { token } = useSelector((state: IChatState) => state);

    useEffect(() => {
        dispatch(getChatList({ profileId }) as any);
    }, [dispatch, profileId, token]);

    return (
        <>
            <SearchProfile profileId={profileId} className="mb-2" />
            <ChatList />
        </>
    );
};
