interface LoaderProps {
    size?: string;
    align?: 'left' | 'center' | 'right';
    className?: string;
}

export const Loader = ({ align, size, className = '' }: LoaderProps) => {
    return (
        <div className={className + (align && ` text-${align}`)}>
            <div
                className="spinner-border"
                role="status"
                style={{ width: size, height: size }}
            >
                <span className="visually-hidden">Loading...</span>
            </div>
        </div>
    );
};
