import { IChatState } from './../common/models/index';
import { useSelector, useDispatch } from 'react-redux';
import axios from 'axios';
import { useEffect, useState } from 'react';
import { setIsLoading, setToken } from '../features/chat/chatSlice';
export const useGetProfile = (profileId: string | undefined) => {
    const [profile, setProfile] = useState({ name: '', age: '' });
    const dispatch = useDispatch();
    const { token } = useSelector((state: IChatState) => state);

    useEffect(() => {
        if (!profileId) {
            setProfile({ name: '', age: '' });
            return;
        }
        axios
            .get('https://search.api.dc2.a4sex.net/api/users/' + profileId, {
                headers: {
                    Authorization: 'Bearer ' + token,
                },
            })
            .then(res => {
                setProfile({
                    name: res.data.name ?? 'Unknown',
                    age: res.data.age ?? 'unknown',
                });
            })
            .catch(error => {
                if (error.response) {
                    if (error.response.status === 401) {
                        dispatch(setToken(''));
                        dispatch(setIsLoading(false));
                    } else console.log(error.response);
                }
            });
    }, [dispatch, token, profileId]);

    return profile;
};
